export default "import { useWindowValue } from '@lunit/new-window';\nimport { Button, TextField } from '@material-ui/core';\nimport React from 'react';\nimport { WindowValue } from '../model/window-value';\n\nexport function WindowContent() {\n  // <NewWindow value={}> 에서 전달한 값들을 받는다\n  const value: WindowValue | undefined = useWindowValue<WindowValue>();\n\n  return value ? (\n    <div>\n      <div>\n        <TextField value={value.a} onChange={({ target }) => value.updateA(+target.value)} /> +{' '}\n        <TextField value={value.b} onChange={({ target }) => value.updateB(+target.value)} /> ={' '}\n        <span>{value.a + value.b}</span>\n      </div>\n\n      <div style={{ marginTop: 20 }}>\n        {/* Window를 닫는다 */}\n        <Button variant=\"contained\" color=\"primary\" onClick={() => window.close()}>\n          Close Window\n        </Button>\n      </div>\n    </div>\n  ) : null;\n}\n"