export default "import { NewWindow } from '@lunit/new-window';\nimport { Button, TextField } from '@material-ui/core';\nimport React, { useState } from 'react';\n\nexport function MainContent() {\n  // window open\n  const [open, setOpen] = useState<boolean>(false);\n\n  // values\n  const [a, setA] = useState<number>(1);\n  const [b, setB] = useState<number>(2);\n\n  return (\n    <div>\n      <div>\n        <TextField value={a} onChange={({ target }) => setA(+target.value)} /> +{' '}\n        <TextField value={b} onChange={({ target }) => setB(+target.value)} /> = <span>{a + b}</span>\n      </div>\n\n      {open ? (\n        <>\n          <div style={{ marginTop: 20 }}>\n            {/* Window를 닫는다 */}\n            <Button variant=\"contained\" color=\"primary\" onClick={() => setOpen(false)}>\n              Close Window\n            </Button>\n          </div>\n\n          {/* Window */}\n          <NewWindow\n            url=\"/window\"\n            value={{\n              a,\n              b,\n              updateA: setA,\n              updateB: setB,\n            }}\n            features={{ width: 500, height: 400 }}\n            onOpen={(popupWindow: Window) => console.log(popupWindow)}\n            onClose={() => setOpen(false)}\n            onBlock={() => {\n              alert('Browser Pop-up 제한을 해제해주세요.');\n              setOpen(false);\n            }}\n          />\n        </>\n      ) : (\n        <div style={{ marginTop: 20 }}>\n          {/* Window를 연다 */}\n          <Button variant=\"contained\" color=\"primary\" onClick={() => setOpen(true)}>\n            Open Window\n          </Button>\n        </div>\n      )}\n    </div>\n  );\n}\n"